:root {
    --Text-Primary: #FFF;
}

:root {
    --Text-Secondry: rgba(255, 255, 255, 0.75);
}


.order-add-cart-block {
    width: 40%;
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.20);
    background: #262328;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    margin-top: 1%;
    font-family: 'TTFirsNeue Regular';
    font-style: normal;
    font-weight: 400;
    padding: 20px;
}

form {
    width: 100%;
}

form > a {
    cursor: pointer;
    width: 100%;
}

form > a > img {
    width: 100%;
}

form > a:hover img {
    filter: brightness(0.8);
}
.small-image {
    display: none;
}
.sbp-button {
    width: 100%;
    text-align: center;
    margin-top: 5%;
    margin-bottom: 5%;
}

.sbp-button a {
    width: 100%;
    display: block;
}

.sbp-button a > img {
    width: 90%;
}

.add-cart-block {
    width: 100%;
}

.label-text {
    width: 100%;
    background-color: #ED8C201A;
    border-radius: 10px;
    text-align: center;
    height: 48px;
    margin-bottom: 10px;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.label-text p {
    color: #ED8C20;
    font-size: 20px;
    font-weight: 400;
}

.card-number-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.card-number-container::after {
    content: attr(data-card-type);
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    padding: 0 10px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
    color: #000;
}

input[type="text"], input[type="number"], input[type="password"] {
    width: 100%;
    padding: 20px;
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.20);
    background: #211F22;
    color: white;
    font-size: 20px;
}

input[type=text]:focus, input[type="number"]:focus, input[type="password"]:focus {
    outline: 2px solid #E1FF00;
}

input.error {
    border: 1px solid red;

}


.cvv-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
}

.cvv-container input {
    width: 49%;

}

input[type="submit"] {
    width: 100%;
    color: #fff;
    border: none;
    padding: 15px 20px;
    border-radius: 8px;
    background: #8B53E8;
    cursor: pointer;
    font-size: 20px;
    transition: background-color 500ms ease-in-out;
}

input[type="submit"]:hover {
    background-color: #522F8B;
}

.submit-button {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    margin-top: 20px;
}

.accept-text {
    width: 100%;
    text-align: center;
    margin-bottom: 0;
}

.accept-text p {
    color: rgba(255, 255, 255, 0.40);
    font-size: 18px;
}

.accept-text a {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.75);
}

